<script>
	import baiduMap_H5 from "../../components/map/baiduMap_H5.vue"
	import baiduMap_wx from "../../components/map/baiduMap_wx.vue"
	import mySidePage from '../../components/my-sidePage/my-sidePage.vue'
	export default {
		data() {
			return {
				showSidePage: false,
				currentIndex: 0,
				switchIndex: '0'
			}
		},
		components:{
			'baiduMap-H5': baiduMap_H5,
			'baiduMap-wx': baiduMap_wx,
			'my-sidePage': mySidePage
		},
		methods: {
			openMenu(){
				this.showSidePage = !this.showSidePage
			},
			goSearch(){
				
			},
			showMe(){
				console.log('1111')
				uni.getLocation({
					type: 'gcj02',
					success: res=>{
						uni.$emit('map-operation', {
							action: 'setCenter',
							lat: res.latitude,
							lng: res.longitude,
						})
					},
					fail: err=>{
						console.log(err)
					}
				})
				
			},
			onswitchButton(e){
				let index = e.target.dataset.type
				if(!index)return
				this.switchIndex = index
			},
			// 改变 current 时触发
			oncurrentChange(e){
				this.currentIndex = e.detail.current
			}
		},
		onShow(){
			
			
		}
	}
</script>

<style lang="scss">
